.password-eye {
	position: absolute;
  z-index: 2;
	top: 20px;
	right: 24px;
	display: inline-block;
	width: 20px;
	height: 20px;
	background: url('../img/icons/eye_closed.svg') 0 0 no-repeat;
  cursor: pointer;
}
.password-eye.view {
	background: url('../img/icons/eye.svg') 0 0 no-repeat;
}


#register-form {
  & .upload__require-box {
    width: auto;
  }
  & p.small_bold {
    font-size: 14px;
  }
  & h5 {
    font-size: 28px;
  }
  & .fieldset {
    margin-top: 50px;
  }
}

.check {
  position: relative;
  & .just-validate-error-label {
    position: absolute;
    top: -18px;
  }
}

@media only screen and (max-width: 767px) {
  .upload__area {
    width: 200px !important;
    height: 200px;
  }
}

.upload__area_text {
  font-size: 14px;
  line-height: 100%;
}

@media only screen and (max-width: 1023px) {
  .upload__area_text {
    font-size: 12px;
  }
}


// ------- UPLOAD PHOTO
.cr-slider {
  margin: 0 auto;
  border: none !important;
  box-shadow: none !important;
  &:hover {
    box-shadow: none !important;
    border: none !important;
  }
  &:focus {
    box-shadow: none !important;
    border: none !important;
  }
}
