.profile {

  &__body {
  }

  &__hat {
    display: flex;
    justify-content: space-between;
    padding-bottom: 30px;

    @include width-768 {
      flex-direction: column;
    }
  }

  &__title {
    text-align: left;
    font-size: 36px;
    line-height: 100%;
    // max-width: 400px;
    margin: 0;
    // color: #FF4D1C;

    @include width-1200 {
      max-width: 100%;
    }
  }

  & .content-block__more:not(:only-child) {
    margin-top: 0;
  }

  &__btn {
    // max-width: 160px;
    height: fit-content;
    width: fit-content;

    & .content-block-more {
      padding: 15px 25px;
      & svg {
        width: 20px;
        height: 20px;
        margin-left: 10px;
        transition: all 0.3s;
      }
      &:hover {
        // background: #FF4D1C;
        & svg {
          fill: #ffffff;
          transition: all 0.3s;
        }
      }
    }
    &-exit {
      margin-left: 40px;

      @include width-768 {
        margin-left: 0;
        &.content-block__more:not(:only-child) {
          margin-top: 30px;
        }
      }
      & .content-block-more {
        padding: 15px 30px;
        display: flex;
        align-items: center;
        & span {
          font-size: 18px;
          font-weight: 400;
        }
      }
    }
    &-back {
      margin-left: 0;
    }
  }

  &__tabs {
    margin-top: 30px;
  }

  & .tabs .tabs__nav-btn--active {
    background: #040404;
    color: #ffffff;
    scale: 1.06;
    & svg {
      fill: #ffffff;
      transition: all 0.3s;
    }
  }

  &__content {
    // background: #f7f7f7;
    background: #f3f3f3;
    padding: 50px 40px;
    border-radius: 15px;
    margin-top: 30px;

    @include width-768 {
      padding: 30px 20px;
    }
  }
  &__subtitle {
    font-size: 30px;
    font-weight: 500;
    line-height: 100%;
    color: #333333;
    // text-align: center;
    & span {
      color: #FF4D1C;
    }
    @include width-1200 {
      font-size: 25px;
    }
    @include width-768 {
      font-size: 20px;
    }
  }
  &__text {
    display: block;
    font-size: 18px;
    font-weight: 400;
    line-height: 130%;
    color: #333333;
    max-width: 700px;
    // margin: 0 auto;
    margin-top: 30px;
    & p {
      font-size: 18px;
      &:not(:last-child) {
        margin-bottom: 20px;
      }
      & b {
        & span {
          color: #FF4D1C;
        }
      }
    }

    @include width-1200 {
      font-size: 16px;
      margin-top: 20px;
      & p {
        font-size: 16px;
      }
    }
    @include width-768 {
      font-size: 14px;
      & p {
        font-size: 14px;
      }
    }
  }
  & .info__item:first-child {
    margin-top: 30px;
    padding-top: 0;
    margin-bottom: 20px;
  }
  & input, textarea {
    background-color: #ffffff !important;
    box-shadow: inset 0 0 0 2px #e0e0e0;
  }
  & textarea {
    min-height: 200px;
  }

  &-password {
    max-width: 600px;
    // margin: 0 auto;
    &__btn {
      background: #ffffff;
      max-width: 300px;
      // margin: 0 auto;
      @include width-576 {
        max-width: 100%
      }
    }
    & .info__item {
      margin-top: 30px;
    }
  }

  &-ask {
    &__success {
      margin-top: 50px;
      background: #39ac08;
      padding: 20px;
      border-radius: 20px;
      & .profile__subtitle {
        color: #ffffff;
      }
      & .profile__text {
        color: #ffffff;
        margin-top: 20px;
      }
    }
  }

  &-test {
    &__btn {
      &.content-block__more:not(:only-child) {
        margin-top: 30px;
      }
    }
  }

  &__user {
    display: flex;
    align-items: center;

    @include width-420 {
      flex-direction: column;
    }
  }

  &__avatar {
    width: 80px;
    height: 80px;
    margin-right: 20px;

    @include width-420 {
      margin-bottom: 20px;
    }
  }
}

.tabs {
  @include tabs();
  &__nav {
    list-style: none;
    padding-left: 0;
    display: flex;
    margin-bottom: 0;
    margin-left: -20px !important;
    padding-left: 20px !important;
    margin-right: -20px !important;
    padding-right: 20px !important;

    @include width-1200 {
      overflow-x: scroll;
      /* Hide scrollbar for Chrome, Safari and Opera */
      &::-webkit-scrollbar {
        display: none;
      }

      /* Hide scrollbar for IE, Edge and Firefox */
      & {
        -ms-overflow-style: none;  /* IE and Edge */
        scrollbar-width: none;  /* Firefox */
      }
    }
  }

  &__nav-item {
    max-width: 100%;
    width: fit-content;
    flex-shrink: 0;
    &:not(:last-child) {
      margin-right: 20px;

      @include width-1200 {
        margin-right: 10px;
      }
    }
  }

  &__nav-btn {
    font-size: 16px;
  }

  &__content {
  }

  &__panel {
  }
}

.step {
  font-size: 30px;
  line-height: 100%;
  font-weight: 500;
  text-align: center;

  @include width-768 {
    font-size: 25px;
  }
  &__container {
    padding-bottom: 0;
    padding-top: 50px;
  }
  & span {
    color: #FF4D1C;
  }
}

.icon-pencil {
  width: 16px !important;
  height: 16px !important;
}

.profile {
  &__earns {
    display: flex;
    align-items: center;
    & >:last-child {
      margin-left: 60px;

      @include width-960 {
        margin-left: 30px;
      }

      @include width-768 {
        margin-left: 0;
        &.profile__btn-exit.content-block__more:not(:only-child) {
          margin-top: 20px;
        }
      }
    }
    @include width-768 {
      flex-direction: column;
    }
  }
  &__earn {
    margin-left: 0;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3,1fr);
    column-gap: 60px;
    row-gap: 40px;
    margin-top: 60px;

    @include width-1400 {
      grid-template-columns: 1fr 1fr;
    }
    @include width-960 {
      column-gap: 40px;
    }
    @include width-768 {
      grid-template-columns: 1fr;
    }
  }
}

.stock {
  display: block;
  background: #FFFFFF;
  border: 1px solid #E7E8FF;
  border-radius: 30px;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.3s;

  @include hover {
    border: 1px solid #000000;
    box-shadow: 0 0 0 1px #040404 !important;
  }

  &__completed {
    cursor: default;
    @include hover {
      border: 1px solid #E7E8FF;
      box-shadow: none !important;
    }
  }

  &__status {
    position: absolute;
    right: 15px;
    top: 15px;
    background: rgb(255, 255, 255, 0.7);
    border-radius: 70px;
    padding: 8px 10px;
  }

  &__body {
    padding: 0 20px 20px;
    margin-top: -30px;
  }

  &__img {
    display: block;
    height: 218px;
    & img {
      height: 100%;
      width: 100%;
    }
  }

  &__avatars {
    display: flex;
    margin-top: 12px;
    & >:not(:first-child) {
      margin-left: 12px;
    }
  }

  &__avatar {
    width: 70px;
    height: 70px;
    border: 1px solid #A9B0F4;
    border-radius: 10px;
    overflow: hidden;
    & img {
      width: 100%;
      height: 100%;
    }
  }

  &__authors {
    display: block;
    font-weight: 400;
    font-size: 18px;
    line-height: 110%;
    color: #474747;
    margin-top: 12px;
  }

  &__title {
    font-weight: 400;
    font-size: 24px;
    line-height: 110%;
    color: #000000;
    margin-top: 40px;
  }

  &__range {
    position: relative;
    background: #EEEFFD;
    width: 100%;
    height: 6px;
    border-radius: 22px;
    overflow: hidden;
    margin-top: 40px;
    &::before {
      content: '';
      position: absolute;
      left: 0;
      width: 20%;
      height: 100%;
      background: #3344E5;
    }
  }

  &__cost {
    display: flex;
    justify-content: space-between;
    margin-top: 14px;

    @include width-420 {
      flex-direction: column;
    }
  }

  &__collected {
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: #3344E5;
  }

  &__aim {
    margin-left: 10px;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: #000000;

    @include width-420 {
      margin-left: 0;
      margin-top: 10px;
    }
  }
}
