.coll__title {
  font-size: 48px;
  line-height: 100%;
  color: #333333;
  margin-bottom: 50px;
}

.upload__area {
  width: 100%;
  max-width: 500px;
}

.info__cover_upload.info__cover_upload_ava .upload__area {
  width: 200px;
  height: 200px;
}

.info__cover_upload.info__cover_upload_ava .upload__area_text {
  font-size: 14px;
  line-height: 1.2;
}

.pp-fund__title {
  font-size: 16px;
}

.pp-fund__reasons-label {
  font-size: 14px;
  background: #7d87e3;
  color: #ffffff;
}

.pp-fund__item input:checked ~ .pp-fund__label {
  border-color: transparent;
  background-color: #3344E5;
  color: #ffffff;
  & .pp-fund__title {
    color: #ffffff;
  }
  @include hover {
    border-color: transparent;
    background-color: #3344E5;
    color: #ffffff;
  }
  &:active {
    background: #7d87e3;
  }
}

.pp-fund__label {
  @include hover {
    border-color: transparent;
    background-color: #3344E5;
    color: #ffffff;
    & .pp-fund__title {
      color: #ffffff;
    }
  }
}

.pp-fund__label:hover {
  opacity: 1;
}

.pp-fund__item input:checked ~ .pp-fund__label .pp-fund__reasons-label {
  background: #7d87e3;
}

.categories-box__item label {
  border: 1px solid #8A94F3;
  border-radius: 30px;
  padding: 7px 15px;
  color: #8A94F3;
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.3s;
  @include hover {
    background: #8A94F3;
    color: #FFFFFF;
  }
  &:active {
    background: #7d87e3;
  }
}

.categories-box__item input:checked ~ label {
  background: #3344E5;
  color: #FFFFFF;
}
// .categories-box__item.active {
//   background: #3344E5;
//   color: #FFFFFF;
// }

.color_green {
  color: #96F400;
}

.checkbox__text a {
  background: transparent;
  text-decoration: underline;
  color: #3344E5;
  border-bottom: none;
  font-weight: 400;
  @include hover {
    color: #333333;
  }
}

.checkbox__text:before {
  width: 18px;
  height: 18px;
  top: 0;
}

.fieldset {
  & .pp__check {
    margin-top: 15px;
  }
}

.content-block__more:not(:only-child) {
  margin-top: 35px;
}

.pp-content__information {
  margin-top: 10px;
}

.pp-content__information a {
  background: transparent;
  color: #3344E5;
  text-decoration: underline;
  @include hover {
    color: #333333;
    text-decoration: underline;
  }
}

.info__descript {
  margin-bottom: 15px;
  margin-top: -10px;
}

.info__descript span {
  line-height: 100%;
}

.input-group > div, .input-group > .input_box {
  margin-bottom: 0;
}

.input-group__row > div, .input-group__row > .invput_box {
  margin-bottom: 35px;
}

.border-bottom {
  padding-bottom: 35px;
  border-bottom: 1px solid #dee2e6!important;
  &-bold {
    padding-bottom: 35px;
    border-bottom: 2px solid #dee2e6!important;
  }
}

.pp-content__form .fieldset:not(:last-child) {
  margin-bottom: 35px;
}

.info__subtitle {
  margin-bottom: 10px;
}

.input_box.data-input {
  margin-bottom: 0;
}

.input_box_textarea {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  & textarea {
    display: block;
  }
  & .textarea_box {
    width: 100%;
  }
}

.input-group:not(:last-child) {
  margin-bottom: 0;
}

.reasons-box {
  margin-bottom: 0 !important;
}

.info__item:first-child {
  padding-top: 0;
}

.info__item {
  width: 100%;
  margin-bottom: 35px;
}

.info__item:last-child {
  padding-top: 0;
  padding-right: 0;
}

.input-group__row > div, .input-group__row > .invput_box {
  margin-bottom: 0;
}

.pp-fund__label {
  border: 1px solid #8A94F3;
}

@media (max-width: 1420px) {
  .info__item:last-child {
    padding-top: 0;
    padding-left: 0;
    padding-right: 0;
  }
}

.reasons-field {
  margin-top: 20px;
}

@media only screen and (max-width: 767px) {
  .pp-content__information {
    font-size: 12px;
    // margin-top: 35px;
}
}

@media (max-width: 1023px) {
  .pp-fund__title {
    font-size: 14px;
    line-height: 130%;
  }
  .pp-fund__reasons-label {
    font-size: 12px;
  }
  .pp-fund__label {
    padding: 15px 20px 9px;
  }
}

.input-date_box {
  @include width-768 {
    margin-bottom: 35px !important;
  }
}

.info__item__gallery {
  margin-bottom: 0;
}

@media only screen and (max-width: 767px) {
  .input-group__row > div, .input-group__row > .input_box {
    width: 100%;
    margin-bottom: 0;
}
}

.input_box {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.just-validate-error-field {
  box-shadow: inset 0 0 0 1px #ec1744 !important;
  border: 1px solid #ec1744 !important;
}

.error {
  // position: static !important;
  // color: #ec1744 !important;
  // margin-top: 5px !important;
  // font-size: 12px !important;
  // white-space: nowrap !important;
  // pointer-events: none !important;
  z-index: 2;
}

.input-group__row {
  align-items: flex-start;
}

.textarea_box {
  & .error {
    top: -10px;
    position: static !important;
  }
}


.profile__btn-exit .content-block-more {
  padding: 15px 30px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.profile__btn-back {
  margin-top: 0;
  width: fit-content;
  & svg {
    width: 30px;
    height: 20px;
    margin-right: 10px;
    transition: all 0.3s;
  }
  &:hover {
    & svg {
      fill: #ffffff;
    }
  }
}

.profile__btn-exit .content-block-more span {
  font-size: 18px;
  font-weight: 400;
}

.input-disabled {
  opacity: 0.5;
  cursor: not-allowed;
  &:hover {
    box-shadow: inset 0 0 0 1px #ffffff !important;
    border: 1px solid #3344E5 !important;
  }
  &:focus {
    box-shadow: inset 0 0 0 1px #ffffff !important;
    border: 1px solid #3344E5 !important;
  }
}

.upload-disabled {
  opacity: 0.5;
  cursor: not-allowed;
  &:hover {
    border-color: #f2f2f2 !important;
  }
}

.upload__area {
  overflow: hidden;
}

.image-preview {
  background-size: contain;
  max-width: 500px;
  height: 250px;
  @include width-1200 {
    max-width: 400px;
    height: 200px;
  }
}

.upload__area-cover {
  max-width: 500px;
  height: 250px;
  @include width-1200 {
    max-width: 400px;
    height: 200px;
  }
  @include width-576 {
    max-width: 300px !important;
    height: 150px;
  }
}

@media only screen and (max-width: 767px) {
  .upload__area {
    width: 200px !important;
    height: 200px;
  }
  .upload__area.upload__area-cover {
    width: 400px !important;
    height: 200px;
  }
  .upload__area-cover .image-preview {
    width: 400px !important;
    height: 200px;
  }
}

@media only screen and (max-width: 575px) {
  .upload__area.upload__area-cover {
    width: 300px !important;
    height: 150px;
  }
  .upload__area-cover .image-preview {
    width: 300px !important;
    height: 150px;
  }
}

@media only screen and (max-width: 419px) {
  .upload__area.upload__area-cover {
    width: 250px !important;
    height: 125px;
  }
  .upload__area-cover .image-preview {
    width: 250px !important;
    height: 125px;
  }
}

.upload__require-box {
  width: fit-content;
}

.input-file-gallery {
  display: grid;
  grid-template-columns: repeat(4,1fr);
  align-items: center;
  gap: 20px;

  @include width-960 {
    grid-template-columns: 1fr 1fr;
  }
  @include width-768 {
    gap: 10px;
  }

  & .upload__require-box {
    margin-left: 0;
  }
}

.gallery__item {
  display: grid;
  // gap: 30px;
}

.gallery-img {
  position: relative;
  cursor: grab;

  &:active {
    cursor: grabbing;
  }
  &__close {
    position: absolute;
    top: 5px;
    right: 5px;
    width: 30px;
    height: 30px;
    background: #000000;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.3s;
    opacity: 0.6;
    & img {
      width: 10px;
      height: 10px;
      transition: all 0.3s;
    }
    &:hover {
      opacity: 1;
      width: 40px;
      height: 40px;
      & img {
        width: 15px;
        height: 15px;
      }
    }
  }
}

.hide {
  display: none;
}

.hovered {
  border-top: 2px solid #36f8db;
}

.coll-success {
  width: fit-content;
  margin: 0 auto;
  &__title {
    font-size: 50px;
    color: #3344E5;
    & svg {
      width: 80px;
      height: 80px;
      margin-left: 20px;
      fill: none;
      stroke: #3344E5;
      stroke-width: 40px;
      margin-bottom: -10px;
    }
  }
  & span {
    font-size: 20px;
  }
}

.coll__user {
  display: grid;
  align-items: center;
  grid-template-columns: 300px 1fr;
  gap: 50px;

  & >:first-child {
    margin-bottom: 0;
  }

  @include width-960 {
    gap: 0;
    grid-template-columns: 240px 1fr;
  }
  @include width-768 {
    grid-template-columns: 1fr;
  }
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;}


.coll-moderate {
  background: #ea1b30;
  padding: 20px;
  border-radius: 20px;
  color: #ffffff;
  font-weight: 400;
  font-size: 18px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 5px;
  margin-bottom: 40px;

  &__item {
    & span {
      text-decoration: underline;
    }
  }
}

.input-date {
  cursor: pointer;
}
