
.vol_anketa_fields_item {
  display: grid;
  grid-template-columns: auto 400px;
  gap: 50px;
  justify-content: space-between;
  align-items: center;
  &:not(:last-child) {
    margin-bottom: 15px;
    padding-bottom: 15px;
    border-bottom: 1px solid #aeaeae;
  }
  &_title {
    font-size: 18px;
    font-weight: 500;
    max-width: 500px;
    color: #333333;
  }
  &_value {
    display: block;
    padding: 0;
    border: transparent !important;
    border-radius: 0;
    box-shadow: transparent;
    min-height: 25px !important;
    width: 100%;

    font-size: 18px;
    font-weight: 400;
    max-width: 400px;
    text-align: end;
    color: #898989;

    &:hover {
      box-shadow: none !important;
      border: transparent !important;
    }

    &:focus {
      box-shadow: none !important;
      border: transparent !important;
    }
  }
}

.manage {
  &__btns {
    margin-top: 50px;
    display: flex;
    flex-wrap: wrap;
    gap: 20px;

    & .content-block__more {
      height: 45px;
      width: fit-content;
      margin-top: 0;
      & button {
        padding: 0 40px;
        background: #000000;

        & span {
          font-weight: 600;
          color: #ffffff;
          font-size: 16px;
        }
        &:hover {
          transform:scale(0.95);
        }
      }
    }
  }
}
