.enter {
  max-width: 500px;
  margin: 0 auto;
  // margin-top: 100px;
  // margin-bottom: 100px;
  & input {
    &::placeholder {
      color: #9e9e9e;
    }
  }
  &__title {
    font-size: 48px;
    color: #333333;
    text-align: center;
    margin-bottom: 50px;
    & span {
      color: #3344E5;
    }

    @include width-1200 {
      font-size: 38px;
      max-width: 400px;
      margin: 0 auto;
      margin-bottom: 40px;
    }
    @include width-768 {
      margin-bottom: 40px;
    }
  }

  & .info__item:first-child {
    padding-top: 0;
  }
  &__container {
    padding-top: 0;
    padding-bottom: 0;
    margin-top: 50px;
  }
}

.forget-password {
  display: block;
  width: fit-content;
  margin-left: auto;
  margin-top: -10px;
  color: #333333;
  &:hover {
    color: #FF4D1C;
    text-decoration: none;
  }
}

.login {
  & .pp__check {
    margin-top: 30px;
  }
  & .pp-check__text span {
    font-size: 14px;
    margin-top: 3px;
    @include width-768 {
      margin-top: 5px;
    }
  }
  & .profile-password__btn {
    max-width: 100%;
  }
}

.enter__register {
  display: block;
  width: fit-content;
  margin: 0 auto;
  margin-top: 10px;
  & a {
    text-decoration: underline;
    color: #3344E5;
    background: transparent;
    &:hover {
      color: #333333;
    }
  }
}

.enter__sent {
  margin-top: 50px;
  background: #39ac08;
  padding: 20px;
  border-radius: 20px;
  color: #ffffff;
}
