
.cropp {
  & .modal-title {
    margin-bottom: 20px;
    font-size: 30px;
    line-height: 100%;
  }
  & .modal-footer {
    width: fit-content;
    margin-left: auto;
    display: flex;
    gap: 15px;
  }
  & .modal-body {
    padding-bottom: 10px;
    margin-bottom: 20px;
  }

  & .croppie-container .cr-boundary {
    width: 100% !important;
  }

  & .content-block__more {
    width: fit-content;
    height: auto;
    margin-top: 0;
    & .btn {
      padding: 6px 12px;
    }
    & span {
      font-size: 16px;
      font-weight: 400;
    }
  }
}

.cropp-ava {
  @include width-768 {
    & .croppie-container .cr-viewport, .croppie-container .cr-resizer {
      width: 200px !important;
      height: 200px !important;
    }
    & .croppie-container .cr-overlay {
      // height: 200px !important;
    }
  }
}

.cropp-cover {
  @include width-768 {
    & .croppie-container .cr-viewport, .croppie-container .cr-resizer {
      width: 200px !important;
      height: 100px !important;
    }
    & .croppie-container .cr-overlay {
      // height: 100px !important;
    }
  }
}

.graph-modal__container {
  max-width: 600px;
  padding: 30px;
  border-radius: 20px;

  @include width-768 {
    padding: 20px;
  }

  & .graph-modal__close {
    top: 15px;
    right: 15px;
  }
}

.popup-moderate {
  max-width: 1000px;
  padding: 40px;

  @include width-768 {
    padding: 50px 20px 20px;
  }

  & h3 {
    font-weight: 700;
    font-size: 30px;
    line-height: 130%;
    margin-bottom: 30px;

    & span {
      color: #3344E5;
    }
  }

  & textarea {
    &::placeholder {
      line-height: 23px;
    }
  }
}

.popup-published {
  max-width: 1000px;
  padding: 40px;

  @include width-768 {
    padding: 50px 20px 20px;
  }

  &__hat {
    width: fit-content;
    margin: 0 auto;
  }

  & h3 {
    font-weight: 700;
    font-size: 30px;
    line-height: 130%;
    margin-bottom: 30px;

    & span {
      color: #3344E5;
    }
  }

  & textarea {
    &::placeholder {
      line-height: 23px;
    }
  }

  &__link {
    max-width: 400px;
    margin-top: 0 !important;
    margin-bottom: 50px;
  }

  &__title {
    margin-bottom: 20px !important;
  }
}

.popup-success {
  background: #3344E5;
  border: 1px solid #FFFFFF;
  border-radius: 30px;
  max-width: 544px;
  padding: 92px 20px;

  & h2 {
    width: fit-content;
    font-weight: 400;
    font-size: 70px;
    line-height: 100%;
    color: #96F400;
    display: flex;
    text-align: center;
    margin: 0 auto;

    @include width-768 {
      font-size: 40px;
    }

    & svg {
      width: 60px;
      height: 56px;
      margin-left: 20px;
      fill: none;
      stroke: #96F400;
      stroke-width: 40px;
      margin-bottom: -10px;

      @include width-768 {
        width: 44px;
        height: 42px;
        margin-left: 10px;
        margin-top: -10px;
      }
    }
  }

  & span {
    display: block;
    font-weight: 400;
    font-size: 18px;
    line-height: 140%;
    text-align: center;
    color: #FFFFFF;
    margin-top: 20px;

    @include width-768 {
      font-size: 14px;
    }
  }

  & .graph-modal__close {
    background-image: url('../img/icons/close.svg');
    background-repeat: no-repeat;
  }
}

.fade:not(.show) {
  opacity: 1;
}

.upload__wrap {
  width: 100%;
  max-width: 300px;
  margin-right: 30px;

  & .upload__area-small .upload__area_text .user-file {
    max-width: 100%;
  }
}

.popup-cancel {
  padding: 40px;

  @include width-768 {
    padding: 50px 20px 20px;
  }

  & textarea::placeholder {
    line-height: 160%;
  }

  & h3 {
    font-weight: 700;
    font-size: 30px;
    line-height: 130%;
    margin-bottom: 30px;

    & span {
      color: #3344E5;
    }
  }
}
